<template>
  <div id="app">
  	<header>
  		<div id="logo" @click="mainpage">
  			<img id = "icon" src = "./assets/icon.jpg">
	  		<span>BeeBee Movie</span>
	  	</div>
      <div>
        <label>城市：</label>
        <select>
          <option>广州</option>
        </select>
      </div>

      <div>
        <router-link to="/movies">电影</router-link> | 
        <router-link to="/cinemas">影院</router-link>
      </div>

      <div>
        <form>
          <input type="text" placeholder="请输入电影名、影院名" v-model="search_str">
          <button class="btn" @click="search(search_str)">搜索</button>
        </form> 
      </div>

	  	<div v-if="login" class="greeting">
			 <router-link to="/account/profile">个人中心</router-link>
			 <button @click="logout" class="btn">退出登录</button>
	    </div>
	    <div v-else class="greeting">
	    	<router-link to="/welcome/signin">登陆</router-link> | 
    		<router-link to="/welcome/signup">注册</router-link>
	    </div>
	  		
  	</header>
    <router-view></router-view>
  </div>
</template>

<script>
export default {
  name: 'app',
  data() {
  	return {
      login: document.cookie.length,
      search_str: ''
  	}
  },
  computed: {
  },
  methods: {
    logout: function() {
      this.$http.get('/logout')
      .then(res => {
          console.log(res.data);
          if (document.cookie.length == 0) {
            window.location.reload();
            this.$router.push('/welcome/signin');
          }
        })
      .catch(e => {
        console.log("服务器异常！");
        console.log(e)
      })
    },
    mainpage: function() {
      this.$router.push('/index');
    },
    search: function(str) {
      console.log(str);
      this.$router.push(`/search/${str}`);
    }
  }
}

</script>

<style>

#app {
  font-family: Microsoft Yahei, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: grey;
  height: 100%;
}

header {
	height: 36px;
	display: flex;
	justify-content: space-between;
  flex-direction: row;
	align-items: center;
	border-bottom: 2px #ffff8c solid;
}
#icon {
    height: 30px;
    width: 30px;
}
#logo *{
	vertical-align: middle;
}

#logo:hover {
  cursor: pointer;
}
.greeting {
    display: flex;
    align-items: center;
  }
.btn {
  padding:0 10px 0 10px;
	background-color: white;
	border: dotted 3px #ffff8c;
	border-radius:5px;
	word-break:keep-all;           /* 不换行 */
	white-space:nowrap; 
	color: #ff8585;
}

.btn:hover {
	color: yellow;
	cursor:pointer
}

  a {
    text-decoration:none;
    color: #b9b9b9;
  }

  a:visited {
    color: #b9b9b9;
  }

  a:hover {
    color: lightgrey;
  }
h1 {
  color: #ffff8c;
}
</style>



