<template>
  <div id = "movies">
    <h1>请选择电影</h1>
    <div class="select-bar">

      <div class="type">
        <label>类型</label>
        <ul class="select-line">
          <li v-for = "type in types" class="select-item" @click="submitType($event, type)">{{type}}</li>
        </ul>
      </div>
        
      <div class="area">
        <label>区域</label>
        <ul class="select-line">
          <li v-for= "area in areas" class="select-item" @click="submitArea($event, area)">{{area}}</li>
        </ul>
      </div>

      <div class="sort-bar">
        <label>排序顺序</label>
        <select v-model="sortSelect">
          <option v-for="sort in sorts" class="select-item">{{sort}}</option>
        </select>
      </div>
    </div>

    <div class="container">
    <ul class="movie-list">
      <li class="movie-item" v-for="movie in movies">
        <img :src="movie.poster">
        <p class="movietitle">{{movie.title}}</p>
        <button @click="buyTicket(movie.title)" class="btn buy_ticket">购票</button>
      </li>
    </ul>

    </div>
  </div>
</template>


<script>
import hot from '../components/hot'
import coming from '../components/coming'
import guess from '../components/guess'
import photoslider from '../components/photoslider'
export default {
  name: 'movies',
  data() {
  	return {
  		username:" ",
      types: ["爱情","喜剧","冒险","恐怖","动作","其他"],
      areas: ["大陆","美国","日本","其他"],
      sorts: ["热门","时间","评价"],
      selected: {
        types: [],
        areas: []
      },
      sortSelect: '热门',
      sortSelectEn: 'Hot',
      movies: [{
        poster: require('../assets/poster/poster1.jpg'),
        title: "海边的曼彻斯特"
      }
      ]
  	}
  },
  watch: {
    sortSelect: function(val) {
      if (val == '热门') {
        this.sortSelectEn = 'Hot';
      } else if (val == '时间') {
        this.sortSelectEn = 'Time';
      } else if (val == '评价') {
        this.sortSelectEn = 'Rank';
      }
    },
    sortSelectEn: function(){
      this.submit();
    }
  },
  methods: {
    submitType: function(e, type) {
      var target = e.target;
      var classnames = target.className.split(' ');
      var classNamePos = classnames.indexOf('selected');
      if(classNamePos == -1) {
        classnames.push('selected');
        this.selected.types.push(type);
      } else {
        classnames.splice(classNamePos, 1);
        var typePos = this.selected.types.indexOf(type);
        this.selected.types.splice(typePos, 1);
      }
      target.className = classnames.join(' ');
      this.submit();
    },
    submitArea: function(e, area) {
      var target = e.target;
      var classnames = target.className.split(' ');
      var classNamePos = classnames.indexOf('selected');
      if(classNamePos == -1) {
        classnames.push('selected');
        this.selected.areas.push(area);
      } else {
        classnames.splice(classNamePos, 1);
        var areaPos = this.selected.areas.indexOf(area);
        this.selected.areas.splice(areaPos, 1);
      }
      target.className = classnames.join(' ');
      this.submit();
    },
    submit: function() {
      this.$http.post('movie/SelectMovies/'+this.sortSelectEn, this.selected)
        .then(res => {
          console.log("获取电影成功");
          this.movies = res.data;
          this.movies.forEach(function(item){
            let posUrl = item.poster;
            item.poster = require("../assets/poster/" + posUrl + ".jpg");
          });
        })
        .catch(e => {
          console.log(e);
        })
    },
    buyTicket: function(title){
      this.$router.push(`/movie/detail/${title}`);
    }
  }
}

</script>

<style scoped>
img {
  width: 10em;
}
#movies {
  width: 1000px;
  margin: 20px auto;
}
.select-line {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
}

.movie-list {
  list-style-type: none;
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
}
.movie-item {
  position: relative;
  border: 1px #ffff8c solid;
  padding: 20px;
  text-align: center;
  margin:10px;
  width: 220px;
  height: 300px;
}
ul { 
  list-style-type: none;
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
}

.select-item {
  margin: 0 5px;
  font-size: 8pt;
}
.select-item:hover {
  color: lightgrey;
  cursor: pointer;
}
.selected {
  color: #ff8585;
}
.buy_ticket {
  position: absolute;
  right: 5px;
  bottom: 5px;
}
.sort-bar {
  text-align: right;
}
select {
  margin-right: 50px;
}
</style>


