<template>
  <div id = "signup">
    <p class="em">{{errMsg}}</p>
    <form @submit.prevent="validateBeforeSubmit">
      <div class="form-container">
        <div class="form-line">
          <label class="label">邮箱</label>
          <input name="email" v-model="user.email" class="email text" placeholder="email address" v-validate:email.initial="'required|email'" @blur="checkEmail">
          <span v-show="errors.has('email')">{{ errors.first('email') }}</span>
        </div>
        <div class="form-line">
          <label class="label">密码</label>
          <input type="password" name="password" class="text" v-validate:password.initial="'required'" v-model="user.password" placeholder="password">
          <span v-show="errors.has('password')">{{ errors.first('password') }}</span>
        </div>
        <div class="form-line">
          <label class="label">重复密码</label>
          <input type="password" name="repeated" class="text" placeholder="repeat password" v-validate:repeated.initial="'required|confirmed:password'" >
          <span v-show="errors.has('repeated')">{{ errors.first('repeated') }}</span>
        </div>
        <div class="form-line">
          <label class="label">昵称</label>
          <input type="text" name="nickname" class="text" v-model="user.nickname" placeholder="nickname">
        </div>
        <div class="form-line">
          <label class="label">手机</label>
          <input type="text" name="phone" class="text" v-model="user.phone" v-validate:phone.initial="'numeric'" placeholder="phone number">
          <span v-show="errors.has('phone')">{{ errors.first('phone') }}</span>
        </div>
        <div class="form-line">
        <label class="label">喜好</label>
          <div class="per">
            <input type="checkbox" id="Romance" value="Romance" v-model="user.preference">
            <label for="Romance">爱情</label>
            <input type="checkbox" id="Comedy" value="Comedy" v-model="user.preference">
            <label for="Comedy">喜剧</label>
            <input type="checkbox" id="Adventure" value="Adventure" v-model="user.preference">
            <label for="Adventure">冒险</label>
            <input type="checkbox" id="Horror" value="Horror" v-model="user.preference">
            <label for="Horror">恐怖</label>
            <input type="checkbox" id="Action" value="Action" v-model="user.preference">
            <label for="Action">动作</label>
            <input type="checkbox" id="Others" value="Others" v-model="user.preference">
            <label for="Others">其它</label>
          </div> 
          <span></span>
        </div>
      </div>

      <input class="btn" type="submit" value="注册">
    </form> 
  </div>
</template>

<script>
export default {
  name: 'signup',
  data() {
    return {
      user: {
        email: '',
        password: '',
        phone: '',
        nickname:'',
        preference: []
      },
      email_vaild: false,
      errMsg: ''
    }
  },
  methods: {
    checkEmail: function() {
      var fData = {
        email: document.querySelector(".email").value
      }
      this.$http.post('/checkemail', fData) //user/CheckEmail
      .then(res=>{
        if (res.status == 200){
          this.errMsg = '';
          this.email_valid = true;
        }
      })
      .catch(e => {
        if (e.status == 409) {
          this.errMsg = '用户已存在';
          this.email_valid = false;
        }
      })
    },
    validateBeforeSubmit: function() {
      this.$validator.validateAll().then(() => {
        //通过验证
        if(!this.email_valid) return; //邮箱已存在
        else {
          this.$http.post('/register', this.user)
          .then(res => {
            if (res.status == 200) {
              console.log('注册成功');
              window.location.reload();
              this.$router.push('/index');
            }
          })
          .catch(e => {
              this.errMsg = "注册失败";
              console.log("服务器异常！");
              console.log(e)
            })
        }
      }).catch(() => {
        // 没通过验证
        alert('资料没填对啊!');
      });
    }
  }
}
</script>

<style scoped>

.em {
  text-align: center;
  margin-right: 10px;
  color: red;
}
form {
  display: flex;
  flex-direction:column;
  margin: 0 auto;
}

.form-line {
  display: flex;
  flex-direction:row;
  justify-content: flex-start;
  width: 450px;
  margin-bottom: 10px;
}

.label {
  width: 100px;
  font-size: 10pt;
  text-align: right;
}
.text {
  width: 150px;
  font-size: 8pt;
  border: 3px double #ffff8c;
  border-radius: 5px;
  height: 15px;
  margin-left: 10px;
}
span {
  font-size: 8pt;
  color: red;
  width: 200px;
  text-align: left;
}

.form-container {
  margin: 0 auto;
}
.per {
  margin-left: 10px;
  width: 170px;
  font-size: 8pt;
  display: inline-block;
}

.btn {
  width: 80px;
  margin: 0 auto;
}
</style>


