<template>
  <div id = "cinema">
    <h1>请选择影院</h1>
    <div class="select-bar">

        <div class="date">
          <label>日期</label>
          <ul class="select-line date-list">
            <li v-for = "day in days" class="select-item" @click="selected($event)">{{day}}</li>
          </ul>
        </div>
    		
        <div class="area">
          <label>区域</label>
          <ul class="select-line area-list">
            <li v-for= "area in areas" class="select-item" @click="selected($event)">{{area}}</li>
          </ul>
        </div>

        <div class="cinema">
          <label>影院</label>
          <ul class="select-line cinema-list">
            <li v-for="cinema in cinemas" class="select-item" @click="selected($event)">{{cinema}}</li>
          </ul>
        </div>


        <div class="sort-bar">
          <label>排序顺序</label>
          <select v-model="sortSelect">
            <option v-for="sort in sorts" class="select-item">{{sort}}</option>
          </select>
        </div>
    </div>

    <div class="movies">
      <ul class="movies-list">
        <li v-for="movie in movies" class="movie-item">
          <div class="movie-left-part">
            <img :src="movie.poster">
            <p class="movietitle">{{movie.title}}</p>
          </div>
          
          <ul class="session-list">
            <li v-for="session in movie.sessions" class="session-item">
              <div class="session-inner-box">
                <span class='time'>{{session.time}}</span>
                <span class='ticket-count'><label>剩余票数：</label>{{session.tickets}}张</span>
                <span class='price'>{{session.price}}元</span>
              </div>
              
              <button class="btn book_btn">订座</button>
            </li>
          </ul>
      </li>
      </ul>
    </div>
  </div>
</template>



<script>
export default {
  name: 'cinema',
  data() {
  	return {
  		movies: [{
        poster: require('../assets/poster/poster1.jpg'),
        title: "海边的曼彻斯特",
        sessions:[{
          time: '12:00',
          price: '34',
          tickets: 0
        }]
      }],
      days:[],
      cinemas: [],
      sorts: ["热门","时间","评价"],
      areas: ["增城市","白云区","天河区","海珠区","越秀区","荔湾区","黄埔区","番禺区","花都区","南沙区","从化市"],
      getMovie_data: {
        date: '',
        area: '',
        cinema: ''
      },
      getCinema_data:{
        data: '',
        area: ''
      },
      sortSelect: '热门',
      sortSelectEn: 'Hot',
  	}
  },
  created: function(){
    this.getDays();
    this.getAllCinemas();
  },
  // mounted: function(){
  //   this.init();
  // },
  watch: {
    sortSelect: function(val) {
      if (val == '热门') {
        this.sortSelectEn = 'Hot';
      } else if (val == '时间') {
        this.sortSelectEn = 'Time';
      } else if (val == '评价') {
        this.sortSelectEn = 'Rank';
      }
    },
    sortSelectEn: function(){
      this.submit();
    }
  },
  methods: {
  	getDayStr: function(addDayCount) {
    	var dd = new Date();
		  dd.setDate(dd.getDate()+addDayCount);
		  var m = dd.getMonth()+1;
		  var d = dd.getDate();
    	return m+"月"+d+"日";
    },
    buyTicket: function(title) {
      this.$router.push(`/movie/detail/${title}`);
    },
    getDays: function() {
      for(var i = 0; i < 7; i++) {
        var tDay = this.getDayStr(i);
        this.days.push(tDay);
      }
      this.getMovie_data.date = this.getDayStr(0);
    },
    getAllCinemas: function() {
      this.$http.get('/movie/AllCinemas')
        .then(res => {
          console.log("获取影院信息成功");
          this.cinemas = res.data;
          this.getMovie_data.cinema = this.cinemas[0];
          setTimeout(this.init,500);
        })
        .catch(e => {
          console.log(e);
        })
    },
    selected: function(e) {
      var target = e.target;
      var selected_type = target.parentNode.parentNode.className;
      this.clearSelected(target.parentNode);
      target.className += ' selected';

      if(selected_type == 'date') {
        this.getMovie_data.date = target.textContent;
        this.getCinema_data.date = target.textContent;
        this.getCinema();
      } else if (selected_type == 'area') {
        this.getMovie_data.area = target.textContent;
        this.getCinema_data.area = target.textContent;
        this.getCinema();
      } else if (selected_type == 'cinema') {
        this.getMovie_data.cinema = target.textContent;
      }
      console.log(this.getMovie_data);
      this.submit();
    },
    clearSelected: function(parent) {
      var children = parent.children;
      for(var i = 0; i < children.length; i++) {
        var classnames = children[i].className.split(' ');
        var pos = classnames.indexOf('selected');
        if (pos != -1) {
          classnames.splice(pos, 1);
          children[i].className = classnames.join(' ');
        }
      }
    },
    submit: function() {
      this.$http.post('movie/MovieWithSession/'+this.sortSelectEn, this.getMovie_data)
        .then(res => {
          console.log("获取电影场次成功");
          this.movies = res.data;
          this.movies.forEach(function(item){
            let posUrl = item.poster;
            item.poster = require("../assets/poster/" + posUrl + ".jpg");
          });
        })
        .catch(e => {
          console.log(e);
        })
    },
    getCinema: function() {
      this.$http.post('movie/Cinemas', this.getCinema_data)
        .then(res => {
          console.log("更新电影院成功");
          this.cinema = res.data;
        })
        .catch(e => {
          console.log(e);
        })
    },
    init: function() {
      this.getMovie_data.area = '天河区';
      this.submit();
      var date = document.querySelector('.date-list');
      var area = document.querySelector('.area-list');
      var cinema = document.querySelector('.cinema-list');
      date.firstElementChild.className += ' selected';
      area.firstElementChild.className += ' selected';
      cinema.firstElementChild.className += ' selected';
    }
  }
}
</script>



<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#cinema {
  width: 1000px;
  margin: 0 auto;
}

.select-line {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
}


ul { 
  list-style-type: none;
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
}

.select-item {
  margin: 0 5px;
  font-size: 8pt;
}
.select-item:hover {
  color: lightgrey;
  cursor: pointer;
}
.movie-list {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}
.movie-item {
  position: relative;
  border: 1px #ffff8c solid;
  padding: 20px;
  text-align: center;
  width: 100%;
  margin:10px;
}
.movie-left-part {
  float: left;
}
img {
  width: 160px;
}
.selected {
  color: #ff8585;
}
.buy_ticket {
  position: absolute;
  right: 5px;
  bottom: 5px;
}
.sort-bar {
  text-align: right;
}
select {
  margin-right: 50px;
}
.session-list {
  display: flex;
  flex-direction:column;
  justify-content:space-around;
}
.session-item {
  padding: 5px;
  border-bottom: 1px dotted #ffff8c;
  position: relative;
}
.session-item:last-child {
  border-bottom: none;
}
.book_btn {
  float: right;
  display: block;
}
.price {
  float:  right;
  font-size: 18pt;
  color:  #ff8585;
}
.session-inner-box{
  display: flex;
  justify-content: space-around;
  margin-bottom: 5px;
}
.ticket-count{
  font-size: 10pt;
  color: lightgrey;
}
</style>




