 <template>
  <div id = "signin">
    <p class="msg">{{errMsg}}</p>
    <form @submit.prevent="validateBeforeSubmit">
      <div class="form-container">
        <div class="form-line">
          <label>邮箱：</label>
          <input type="email" v-model="user.email" class="email text" name="email" v-validate:email.initial="'required|email'" placeholder="email address">
          <span v-show="errors.has('email')">{{ errors.first('email') }}</span>
        </div>
        
        <div  class="form-line">
          <label>密码：</label>
          <input type="password" name="password" class="text" v-validate:password.initial="'required'" v-model="user.password" placeholder="password">
          <span v-show="errors.has('password')">{{ errors.first('password') }}</span>
        </div>
      </div>
        <input class="btn" type="submit" value="登陆">
    </form> 
  </div>
</template>

<script>
export default {
  name: 'signin',
  data() {
    return {
      user: {
        email: '',
        password: ''
      },
      errMsg: ''
    }
  },
  methods: {
    validateBeforeSubmit: function() {
      this.$validator.validateAll().then(() => {
        //通过验证
        this.$http.post('/Login', this.user)
        .then(res => {
            console.log('response is' + res.data);
            if (res.status == 200) {
              //登陆成功
              window.location.reload();
              this.$router.push('/index');
            }
          })
        .catch(e => {
          if (e.status == 404) {
            this.errMsg = "用户不存在";
          } else if (e.status == 401) {
            this.errMsg = "密码错误";
          } else {
            this.errMsg = "注册失败";
          }
          console.log("注册失败");
        })
      }).catch(() => {
        // 没通过验证
        alert('资料没填对啊!');
      });
    }
  }
}
</script>

<style scoped>

  form {
    display: flex;
    flex-direction:column;
    margin: 0 auto;
  }
  .form-line {
    display: flex;
    flex-direction:row;
    justify-content: flex-start;
    width: 450px;
    margin-bottom: 10px;
  }
  .form-container {
    margin: 0 auto;
  }
  label {
    width: 100px;
    font-size: 10pt;
    text-align: right;
  }
  .text{
    width: 150px;
    font-size: 8pt;
    border: 3px double #ffff8c;
    border-radius: 5px;
    height: 15px;
    margin-left: 10px;
  }
  span {
  font-size: 8pt;
  color: red;
  width: 200px;
  text-align: left;
}
  .btn {
    width: 80px;
    margin: 0 auto;
  }
  .msg {
    color: red;
    margin-bottom: 0;
  }
  
</style>


